// npx parcel index.html
createElement = function(tag, attrs, ...children){
    // createElementçš„ä¸»è¦åŠŸèƒ½ï¼Œå°±æ˜¯åœ¨babelæŠŠjsxè½¬æ¢ä»¥åŽï¼Œå†æŠŠè½¬æ¢åŽçš„ç»“æžœè¿”å›žä¸ºä¸€ä¸ªobject
    // ä»¥ä¾¿ä¹‹åŽæ“ä½œè¿™ä¸ªobject
    // ä¾‹å¦‚è¿™é‡Œçš„renderå‡½æ•°é‡Œçš„ç¬¬ä¸€ä¸ªå…¥å‚jsx(vnode)å°±ä»Žjsxè½¬ä¸ºobjectäº†

    return {
        tag,
        attrs,
        children
    }
}

const Jreact = {
    createElement
}

const JreactDom = {
    render
}

function render (vnode, container) {
    // renderçš„ä¸»è¦åŠŸèƒ½å°±æ˜¯æŠŠè™šæ‹Ÿdomè½¬æ¢ä¸ºçœŸå®ždom
    // htmlçš„å…³é”®ï¼Œæ— éžå°±æ˜¯ï¼Œtag,å­å…ƒç´ å’Œtagé‡Œçš„å†…å®¹

    // è¿™é‡Œåˆ›å»ºtagé‡Œçš„å†…å®¹
    if(typeof vnode ==='string' || typeof vnode === 'number') {
        return container.appendChild(document.createTextNode(vnode))
    }

    // è¿™é‡Œé€’å½’åˆ›å»ºtagå’Œ
    if(typeof vnode ==='object') {
        let dom = document.createElement(vnode.tag)
        console.log('dom----->', dom)
        setAttribute(dom, vnode.attrs)
        if(vnode.children&&Array.isArray(vnode.children)) {
            vnode.children.forEach(vnodeChild=>{
                render(vnodeChild, dom)
            })
        }
        container.appendChild(dom)
    }
}

function setAttribute(node, attrs) {
    if(!attrs) return
    for(let key in attrs) {
        if(key.startsWith('on')) {
            node[key.toLocaleLowerCase()] = attrs[key]
        } else if(key === 'style') {
            Object.assign(node.style, attrs[key])
        } else {
            node[key] = attrs[key]
        }
    }
}

let name = "jirengu"
function clickBtn() {
    console.log('click me')
}
// console.log(vnode)
JreactDom.render(
    <div className="wrapper">
        <h1>hello {name}</h1>
        <button onClick={clickBtn}>click me</button>
    </div>,document.querySelector('#app'))